import React, {Dispatch, SetStateAction, useRef} from 'react'
import {DateRangePicker} from 'element-react'
import { BsCalendarDate } from 'react-icons/bs'

interface DatePickerParams {
    date: ElementReactLibs.dateType[],
    setDate: Dispatch<SetStateAction<ElementReactLibs.dateType[]>>,
    disabledDate(date: Date): boolean
}

function DatePicker(props: DatePickerParams) {
    const {date, setDate, disabledDate} = props;
    const refDatePicker = useRef();

    const handleCalendarClick = () => {
        // @ts-ignore
        refDatePicker.current.refs.inputRoot.focus();
    };

    return (
        <div className="relative mr-4">
            <DateRangePicker
                // @ts-ignore
                ref={refDatePicker}
                isShowTrigger={false}
                isReadOnly={true}
                value={date}
                disabledDate={disabledDate}
                onChange={setDate}
            />
            <BsCalendarDate className="absolute right-2 top-1/2 transform -translate-y-1/2 text-primary cursor-pointer" onClick={handleCalendarClick}/>
        </div>
    )
}

export default DatePicker;
